import React, { Component } from 'react';
import {Map, InfoWindow, Marker, GoogleApiWrapper} from 'google-maps-react';

class GeolocationExample extends Component {
  constructor(props) {
    super(props);

    this.state = {
      latitude: null,
      longitude: null,
      error: null,
    };
  }

  componentDidMount() {
    this.watchId = navigator.geolocation.watchPosition(
      (position) => {
        this.setState({
          latitude: position.coords.latitude,
          longitude: position.coords.longitude,
          error: null,
        });
      },
      (error) => this.setState({ error: error.message }),
      { enableHighAccuracy: true, timeout: 20000, maximumAge: 0, distanceFilter: 1 },
    );
  }

  componentWillUnmount() {
    navigator.geolocation.clearWatch(this.watchId);
  }

  render() {
    const pos = {lat: this.state.latitude, lng: this.state.longitude};
    return (
      <div style={{ flexGrow: 1, alignItems: 'center', justifyContent: 'center' }}>
        <p>Latitude: {this.state.latitude}</p>
        <p>Longitude: {this.state.longitude}</p>
        {this.state.error ? <p>Error: {this.state.error}</p> : null}
        { this.state.latitude && this.state.longitude ?
        <Map google={this.props.google} zoom={17} initialCenter={pos}  style={{width: '100%', height: '80%', position: 'relative'}}>
          <Marker onClick={this.onMarkerClick}
                  position={pos}
                  name={'Current location'}
                   />
        </Map>:
        ''
      }
      </div>
    );
  }
}

export default GoogleApiWrapper({
  apiKey: ('AIzaSyD-yFn1Ikwk5lPYXMhWXLL4y5d8gbj1t_A')
})(GeolocationExample);



